.container{
    max-width: 900px;
    display: block;
    margin: auto;
    .input-form{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        padding-top: 20px;
        padding-bottom: 20px;
        .save-btn{
            padding: 12px 50px;
            border-radius: 4px;
            background: wheat;
            border: 2px solid green;
            color: green;
            font-size: 14px;
            font-weight: bold;
            text-transform: uppercase;
            outline: none;
            &.cancel{
                border: 2px solid orange;
                color: orange;
            }
        }
        .action-wrap{
            display: flex;
            justify-content: space-between;
            width: 40%;
        }
        .input-title{
            width: 40%;
            background: white;
            border : 1px solid green;
            padding: 20px;
            border-radius: 4px;
            color: gray;
            font-size: 14px;
            margin-bottom: 12px;
            box-sizing: border-box;
            outline: none;
            transition: all ease 0.35s;
            &:hover{
                transform: translateY(1px);
            }
        }
        .input-content{
            width: 40%;
            border: 1px solid green;
            border-radius: 4px;
            box-sizing: border-box;
            padding: 12px;
            min-height: 200px;
            margin-bottom: 16px;
            font-size: 14px;
            color: gray;
            outline: none;
        }
        .save-btn{
            padding: 12px 50px;
            border-radius: 4px;
            background: white;
            border: 2px solid green;
            color: green;
            font-size: 14px;
            font-weight: bold;
            text-transform: uppercase;
            outline: none;
            transition: all ease 0.20s;
            &:hover{
                cursor: pointer;
                background: lightgreen;
                transform: translateY(2px);
            }
        }
    }
    .card-content{
        border: 1px solid black;
        border-radius: 4px;
        padding: 16px;
        box-shadow: 4px 3px 9px black;
        margin-bottom: 16px;
        transition: all ease 0.35s;
        position: relative;
        &:hover{
            cursor: pointer;
            background: lightblue;
            transform: translateY(-2px);
        }
        p{
            margin: 0px;
        }
        .title{
            font-size: 24px;
            font-weight: bold;
        }
        .date{
            font-size: 12px;
        }
        .content{
            margin-top: 20px;
            font-size: 14px;
        }
        .delete-btn{
            width: 30px;
            height: 30px;
            background: red;
            border-radius: 30px;
            display: flex;
            justify-content: center;
            align-items: center;
            font-weight: bold;
            color: white;
            position: absolute;
            top: 1px;
            right: 1px;
            &:hover{
                cursor: pointer;
            }
        }
    }
    .keluar-note{
        cursor: pointer;
        background: yellow;
        width: 60px;
        height: 35px;
        text-align: center;
        border-radius: 12px;
        .text-keluar{
            padding-top: 5px;
        }
    }
}